import React, { useState } from "react";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import "../../css/login.css";
import axios from "axios";

export default function Login() {
  const [userId, setUserId] = useState("");
  const [password, setPassword] = useState("");
  const [errorMessage, setErrorMessage] = useState("");

  const signin = (e) => {
    e.preventDefault();
    if (userId === "") {
      alert(" .");
      return;
    }
    if (password === "") {
      alert(" .");
      return;
    }
    axios
      .post("/api/auth/signin", {
        userid: userId,
        password: password,
      })
      .then((res) => {
        console.log(res.headers);
        window.location.replace("/");
        // console.log(res);
      })
      .catch((err) => {
        if (err.response.status === 404) {
          setErrorMessage("*   .");
        } else if (err.response.status === 401) {
          setErrorMessage("*   .");
        } else {
          alert("error");
          console.log(err);
        }
      });
  };

  const onChange = (e) => {
    if (e.target.id === "textfield-id") setUserId(e.target.value);
    else setPassword(e.target.value);
  };

  return (
    <div className="login">
      <a href="/" className="login_title">
        <h1 className="login_title">  </h1>
      </a>
      <Box
        className="login_box"
        component="form"
        sx={{
          "& > :not(style)": { m: 1, width: "25ch" },
        }}
        noValidate
        autoComplete="off"
      >
        <TextField id="textfield-id" label="" variant="standard" onChange={onChange} />
        <TextField id="textfield-password" label="" variant="standard" type="password" onChange={onChange} />
        <button className="login_button" onClick={signin}>
          
        </button>
      </Box>
      <div className="signup_massege">
        <a>  ?</a>
        <a href="SignUp" className="signup_link">
          
        </a>
      </div>
      <p className="errorMessage">{errorMessage}</p>
    </div>
  );
}
