import axios from "axios";
import "../css/main.css";

export default function OutlineTitle({ userName }) {
  const logout = (e) => {
    e.preventDefault();
    console.log("logout clicked");
    axios.get("/api/auth/logout").then(() => {
      console.log("logout");
      window.location.replace("/");
    });
  };
  return (
    <div className="title">
      <a href="/" className="title_link">
        <h1 className="title_h1">  </h1>
      </a>
      {userName === null ? (
        <a className="login_link" href="Login">
          
        </a>
      ) : (
        <div>
          <a className="login_link"> {userName}!</a>
          <a className="logout" onClick={logout} href="/">
            
          </a>
        </div>
      )}
    </div>
  );
}
